.searchpage_logo{
object-fit: contain;
height: 50px;
margin-right: 50px;
}

.searchpage_header{
    display: flex;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    align-items: flex-start;
    padding: 30px;
    border-bottom: 1px solid lightgray;
}

.searchpage_headerbody > .search > .search_input{
    margin-top: 0;
    width: unset;
    margin: unset;
    max-width: unset;
}

.searchpage_options{
    display: flex;
    align-items: center;
    color: grey;
}

.searchpage_options a,
.searchpage_optionsright a{
    text-decoration: none;
    color: grey;
}

.searchpage_optionsright,
.searchpage_optionsleft{
display: flex;
}

.searchpage_optionsright{
    margin-left: 80px;
}

.searchpage_op{
    display: flex;
    align-items: center;
    margin-right: 20px;
}

.searchpage_op a{
    margin-left: 5px;
}

.searchpage_res{
    margin: 40px 0;
}

.searchpage_resultcount{
    color: #70757a;
    font-size: 14px;
}

.searchpage_results{
    max-width: 650px;
    margin-top: 20px;
    margin-left: 240px;
    margin-bottom: 100px;
}

.searchpage_resultimage{
    object-fit: contain;
    height: 20px;
    width: 20px;
    margin-right: 10px;
}

.searchpage_resulttitle{
    text-decoration: none;
}

.searchpage_resulttitle:hover{
    text-decoration: underline;
}

.searchpage_resulttitle>h2{
    font-weight: 500;
}

.searchpage_resultsnippet{
    margin-top: 10px;
}

